@font-face {
  font-family: 'debug';
  src: url('./DEBUG\ FREE\ TRIAL.ttf');
}


body {
  background: #000000;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mybanner{
  height: 20%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  margin-top: 10px;

}
.mylogoname{
  color: white;
  font-family: debug;
  font-size: 55px;
  position: fixed;
  top: 7%;
  left:43%;

}
.onelogo{
  width: 100px;
  height: 90px;
  display: inline;
  margin-left: 400px;
}

.twologo{
  width: 100px;
  height: 100px;
  display: inline;
  margin-left: 35%;
  margin-right: auto;
}

@media screen and (max-width: 600px) {

  .mybanner{
    height: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    margin-top: 10px;
  
  }
  .onelogo{
    width: 100px;
    height: 100px;
    display: inline;
    margin-left: auto;
    margin-right: auto;
  }
  
  .twologo{
    width: 60px;
    height: 60px;
    display: inline;
    margin-left: 15%;
    margin-right: auto;
  }
  .mylogoname{
    color: white;
    font-family: debug;
    font-size: 45px;
    position: fixed;
    top: 7%;
    left:43%;
  
  }

}